___CSS_LOADER_EXPORT___.push([module.id, "td[data-v-387ed0ad]{min-width:150px}", "",{"version":3,"sources":["webpack://./resources/js/components/dashboard/Users/UserList.vue"],"names":[],"mappings":"AAuOA,oBACI,eACJ","sourcesContent":["<template>\n    <Main>\n        <div class=\"row pt-4\">\n            <Breadcrumb>\n                <li class=\"breadcrumb-item\"><router-link :to=\"{name : 'dashboard'}\" class=\"text-decoration-none\">Home</router-link></li>\n                <li class=\"breadcrumb-item active\" aria-current=\"page\">Users</li>\n            </Breadcrumb>\n            <div class=\"col-12\">\n                <div class=\"card\">\n                    <div class=\"card-header py-3\">\n                        <i class=\"fa fa-list\"></i>\n                        Users List\n                    </div>\n                    <div class=\"card-body table-responsive\">\n                        <table class=\"table table-borderless table-hover\">\n                            <thead>\n                                <tr class=\"table-light\">\n                                    <th>#</th>\n                                    <th>Profile</th>\n                                    <th>Name</th>\n                                    <th>Email</th>\n                                    <th>Email Verify At</th>\n                                    <th>Role</th>\n                                    <th>Address</th>\n                                    <th>City</th>\n                                    <th>State</th>\n                                    <th>Created</th>\n                                    <th>Action</th>\n                                </tr>\n                            </thead>\n                            <tbody>\n                                <tr v-for=\"user in users.data\" :key=\"user.id\">\n                                    <td style=\"min-width: 50px\">\n                                        {{ user.id }}\n                                    </td>\n                                    <td>\n                                        <img\n                                            style=\"width: 100px\"\n                                            :src=\"\n                                                user.profile\n                                                    ? user.profile\n                                                    : '/images/default.png'\n                                            \"\n                                            alt=\"Profile\"\n                                        />\n                                    </td>\n                                    <td>\n                                        {{ user.name }} <br />\n                                        <small\n                                            v-if=\"user.id == User\"\n                                            class=\"text text-success fw-bold\"\n                                            >This is you!</small\n                                        >\n                                    </td>\n                                    <td>{{ user.email }}</td>\n                                    <td>\n                                        <span class=\"small\">\n                                            <i class=\"fa fa-calendar\"></i>\n                                            {{\n                                                dateFormat(\n                                                    user.email_verify_at,\n                                                    \"MMM d YYYY\"\n                                                )\n                                            }}\n                                        </span>\n                                    </td>\n                                    <td>\n                                        {{ user.role.role }}\n                                        <router-link\n                                            :to=\"{\n                                                name: 'user.edit',\n                                                params: { id: user.id },\n                                            }\"\n                                            class=\"ms-3\"\n                                        >\n                                            <i\n                                                class=\"fa fa-pencil text-black\"\n                                            ></i>\n                                        </router-link>\n                                    </td>\n                                    <td>\n                                        {{\n                                            user.address\n                                                ? user.address\n                                                : \"No Order yet\"\n                                        }}\n                                    </td>\n                                    <td>\n                                        {{\n                                            user.city\n                                                ? user.city\n                                                : \"No Order yet\"\n                                        }}\n                                    </td>\n\n                                    <td>\n                                        {{\n                                            user.state\n                                                ? user.state\n                                                : \"No Order yet\"\n                                        }}\n                                    </td>\n                                    <td>\n                                        <span class=\"small\">\n                                            <i class=\"fa fa-calendar\"></i>\n                                            {{\n                                                dateFormat(\n                                                    user.created_at,\n                                                    \"MMM d YYYY\"\n                                                )\n                                            }}\n                                        </span>\n                                        <br />\n                                        <span class=\"small\">\n                                            <i class=\"fa fa-clock\"></i>\n                                            {{\n                                                dateFormat(\n                                                    user.created_at,\n                                                    \"h:mm\"\n                                                )\n                                            }}\n                                        </span>\n                                    </td>\n                                    <td class=\"text-nowrap\">\n                                        <button\n                                            v-if=\"user.id !== User\"\n                                            class=\"btn\"\n                                            type=\"button\"\n                                            data-bs-toggle=\"modal\"\n                                            :data-bs-target=\"`#user${user.id}`\"\n                                        >\n                                            <i\n                                                class=\"fa fa-trash text-danger\"\n                                            ></i>\n                                            Delete\n                                        </button>\n                                        <router-link\n                                            v-if=\"user.id === User\"\n                                            to=\"/dashboard\"\n                                            class=\"btn text fw-bold text-success\"\n                                            type=\"button\"\n                                        >\n                                            <i\n                                                class=\"fa-solid fa-house-user\"\n                                            ></i>\n                                            Dashboard\n                                        </router-link>\n                                        <Model\n                                            :id=\"`user${user.id}`\"\n                                            title=\"User Delete Confirmation\"\n                                            :description=\"`<p>The following changes have been made:</ p>\n                                            <p>Username : <span>${user.name}</span>.</p>\n                                            <p>Email    : ${user.email}.</p>\n                                            Are you sure you want to delete this user?`\"\n                                            v-on:confirm=\"deleteUser(user.id)\"\n                                        />\n                                    </td>\n                                </tr>\n                            </tbody>\n                        </table>\n                        <Pagination\n                            :data=\"users\"\n                            @pagination-change-page=\"getResults\"\n                        ></Pagination>\n                    </div>\n                </div>\n            </div>\n        </div>\n    </Main>\n</template>\n<script>\nimport Main from \"../Layout/Main.vue\";\nimport moment from \"moment\";\nimport LaravelVuePagination from \"laravel-vue-pagination\";\nimport Model from \"../../Profile/Model.vue\";\nimport Breadcrumb from \"../../layouts/Breadcrumb\";\nexport default {\n    name: \"Product-list\",\n    components: {Breadcrumb, Main, Model, moment, Pagination: LaravelVuePagination },\n    data() {\n        return {\n            users: [],\n        };\n    },\n    created() {\n        this.getResults();\n    },\n    methods: {\n        formatCurrency(price) {\n            price = price / 100;\n            return price.toLocaleString(\"en-US\", {\n                style: \"currency\",\n                currency: \"USD\",\n            });\n        },\n        dateFormat(date, format) {\n            return moment(date).format(format);\n        },\n        getResults(page) {\n            if (typeof page === \"undefined\") {\n                page = 1;\n            }\n            axios\n                .get(\"/api/dashboard/users?page=\" + page, {\n                    headers: {\n                        Authorization: `Bearer ${this.$store.state.auth.token}`,\n                    },\n                })\n                .then((response) => {\n                    this.users = response.data;\n                });\n        },\n        deleteUser(id) {\n            axios\n                .delete(\"/api/dashboard/user/delete/\" + id, {\n                    headers: {\n                        Authorization: `Bearer ${this.$store.state.auth.token}`,\n                    },\n                })\n                .then((res) => this.getResults())\n                .catch((err) => console.log(err));\n        },\n    },\n    computed: {\n        User() {\n            return JSON.parse(localStorage.getItem(\"auth\")).user.id;\n        },\n    },\n};\n</script>\n<style scoped>\ntd {\n    min-width: 150px;\n}\n</style>\n"],"sourceRoot":""}]);
